import { useEffect, useRef } from "react";
import "./scss/main.scss";

export default function Main() {
  const mainRef = useRef(),
    totalScroll = useRef();
  let scrollY, currentPart;

  const scrollEvent = () => {
    scrollY = window.scrollY;
    if (mainRef.current.offsetTop <= scrollY) {
      mainRef.current.classList.add("active");
    } else {
      mainRef.current.classList.remove("active");
    }

    currentPart = Math.floor(
      (scrollY * mainRef.current.children.length) /
        mainRef.current.clientHeight -
        1
    );

    if (currentPart < 0) currentPart = 0;
    if (currentPart > mainRef.current.children.length - 1)
      currentPart = mainRef.current.children.length - 1;

    setPart(mainRef.current.children, currentPart);
    console.log();
  };

  useEffect(() => {
    mainRef.current.style.height = `${100 * mainRef.current.children.length}vh`;
    totalScroll.current = window.innerHeight + mainRef.current.clientHeight;

    window.addEventListener("scroll", () => {
      scrollEvent();
    });

    return window.removeEventListener("scroll", () => {
      scrollEvent();
    });
  });
  return (
    <>
      <section id="main" ref={mainRef}>
        <div className="part part1">1</div>
        <div className="part part2">2</div>
        <div className="part part3">3</div>
        <div className="part part4">4</div>
        <div className="part part5">5</div>
        <div className="part part6">6</div>
      </section>
    </>
  );
}

const setPart = (target, num) => {
  Object.values(target).map((item) => {
    item.classList.remove("active");
  });
  target[num].classList.add("active");
};
